* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: url(https://cdn.freecodecamp.org/curriculum/css-cafe/beans.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.container {
  background: #deb887;
  max-width: 1260px;
  margin: 2em auto;
  padding: 2em;
}

// header section
.head-content {
  text-align: center;

  h1 {
    font-size: 3rem;
    text-transform: uppercase;
    line-height: 1em;
    margin-bottom: 0.3em;
  }

  p {
    margin-bottom: 1.2em;
    font-size: 1.3rem;
    font-family: sans-serif;
    font-style: italic;
  }
}

// MAIN SECTION
hr {
  background: #a52a2a;
  color: #a52a2a;
  height: 0.3em;
  width: 100%;
}
